.hand {
    width: 250px;
    height: 250px;
    position: absolute;
    top: 40%;
    left: 43%;
}

@-webkit-keyframes idle {
    0% {
        opacity: 1;
    }
    30% {
        opacity: 1;
    }
    31% {
        opacity: 0;
    }
    41% {
        opacity: 0;
    }
    42% {
        opacity: 1;
    }
    45% {
        opacity: 1;
    }
    46% {
        opacity: 0;
    }
    56% {
        opacity: 0;
    }
    57% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@keyframes idle {
    0% {
        opacity: 1;
    }
    30% {
        opacity: 1;
    }
    31% {
        opacity: 0;
    }
    41% {
        opacity: 0;
    }
    42% {
        opacity: 1;
    }
    45% {
        opacity: 1;
    }
    46% {
        opacity: 0;
    }
    56% {
        opacity: 0;
    }
    57% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

.idle {
    -webkit-animation: idle 2.5s linear infinite;
            animation: idle 2.5s linear infinite;
    -webkit-animation-delay: -3.5s;
            animation-delay: -3.5s;
}


@-webkit-keyframes knock {
    0% {
        opacity: 0;
    }
    30% {
        opacity: 0;
    }
    31% {
        opacity: 1;
    }
    41% {
        opacity: 1;
    }
    42% {
        opacity: 0;
    }
    45% {
        opacity: 0;
    }
    46% {
        opacity: 1;
    }
    56% {
        opacity: 1;
    }
    57% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}


@keyframes knock {
    0% {
        opacity: 0;
    }
    30% {
        opacity: 0;
    }
    31% {
        opacity: 1;
    }
    41% {
        opacity: 1;
    }
    42% {
        opacity: 0;
    }
    45% {
        opacity: 0;
    }
    46% {
        opacity: 1;
    }
    56% {
        opacity: 1;
    }
